import React, { useEffect } from "react";
import * as Utils from "utils/utils.jsx";
import RadioItems from "../../RadioItems";
import ThemeSkeleton from "./theme_skeleton";

const themeStyle = [
  ["#845b74", "#3a3b51"],
  ["#34776f", "#353d51"],
  ["#446e80", "#363c52"],
  ["#5b6a91", "#383d54"]
];

// const colorFormat = idx => `linear-gradient(180deg, ${themeStyle[idx][0]} 0%, ${themeStyle[idx][1]} 100%)`;
const colorVal = val => `${val[0]},${val[1]}`;

const options = [
  {
    label: <ThemeSkeleton linearGradient={themeStyle[0]} />,
    value: colorVal(themeStyle[0])
  },
  {
    label: <ThemeSkeleton linearGradient={themeStyle[1]} />,
    value: colorVal(themeStyle[1])
  },
  {
    label: <ThemeSkeleton linearGradient={themeStyle[2]} />,
    value: colorVal(themeStyle[2])
  },
  {
    label: <ThemeSkeleton linearGradient={themeStyle[3]} />,
    value: colorVal(themeStyle[3])
  }
];

const themeConfig = {
  sidebarBg: '#3a3b51',
  sidebarText: '#ffffff',
  sidebarUnreadText: '#ffffff',
  sidebarTextHoverBg: '#136197',
  sidebarTextActiveBorder: '#7ab0d6',
  sidebarTextActiveColor: '#ffffff',
  sidebarHeaderBg: '#845b74',
  sidebarHeaderTextColor: '#ffffff',
  onlineIndicator: '#7dbe00',
  awayIndicator: '#dcbd4e',
  dndIndicator: '#ff6a6a',
  mentionBj: '#fbfbfb',
  mentionColor: '#2071f7',
  centerChannelBg: '#f2f4f8',
  centerChannelColor: '#333333',
  newMessageSeparator: '#ff8800',
  linkColor: '#2f81b7',
  buttonBg: '#1dacfc',
  buttonColor: '#ffffff',
  errorTextColor: '#a94442',
  mentionHighlightBg: '#f3e197',
  mentionHighlightLink: '#2f81b7',
  codeTheme: "github",
  mentionBg: '#fbfbfb'
};

export default props => {
  let _theme = props.theme;

  const handleSetTheme = async e => {
    const val = e.target.value.split(",");
    const theme = {
      ...themeConfig,
      sidebarHeaderBg: val[0],
      sidebarBg: val[1],
    };
    Utils.applyTheme(theme);
    await props.actions.saveTheme("", theme);
    if (props.applyToAllTeams) {
      await props.actions.deleteTeamSpecificThemes();
    }
  };

  return (
    <RadioItems
      className="theme"
      title=""
      name="theme"
      options={options}
      //defaultValue={`${_theme.sidebarHeaderBg},${_theme.sidebarBg}`}
      defaultValue={(_theme.sidebarHeaderBg !== '#5b6a91' || _theme.sidebarBg !== '#383d54') ? '#5b6a91,#383d54' : `${_theme.sidebarHeaderBg},${_theme.sidebarBg}`}
      onChange={handleSetTheme}
    />
  );
};
