import React, { useState } from 'react';
import { Card, Row, Col, message } from 'antd';
import ProfilePicture from './profile_picture';
import './general.scss';

const Items = ({ dataSource = [] }) => {
    return (
        <Row>
            {dataSource.map((item, idx) => (
                <Col span={12} key={idx}>
                    <label>{item.label}</label>
                    <span title={item.value}>{item.value || ' '}</span>
                </Col>
            ))}
        </Row>
    )
}

export default (props) => {
    // console.log(props);
    const [loadingPicture, setLoadingPicture] = useState(false);
    const { nickname, username, phone, email, position, sex, id, last_picture_update, department_name, job_grade } = props.user || {};
    const handleFileChange = (file) => {
        // console.log(file, '-----profile-picture');
        setLoadingPicture(true);
        props.actions.uploadProfileImage(id, file)
            .then(({data, error: err}) => {
                if (data) {
                    // console.log('file ok');
                } else if (err) {
                    console.error('file fail', err);
                    message.error(err);
                }
                setLoadingPicture(false);
            });
    }
    return (
        <div className="settings__general">
            <Card>
                <div className="user_base_info">
                    <div>
                        <h3>
                            {nickname || username}
                            {sex && <span className={sex === 1 ? 'female' : 'male'} />}
                        </h3>
                        <p>{department_name}</p>
                    </div>
                    <ProfilePicture
                        id={id}
                        lastPictureUpdate={last_picture_update}
                        onFileChange={handleFileChange}
                        loadingPicture={loadingPicture}
                    />
                </div>
                <Items dataSource={[
                    { label: '', value: phone },
                    { label: '', value: department_name },
                    { label: '', value: '' },
                    { label: '', value: position },
                    { label: '', value: email },
                    { label: '', value: job_grade },
                ]} />
            </Card>
        </div>
    );
}