import React from 'react';
import { Radio } from 'antd';

export default ({ title, options = [], ...rest }) => {
  return (
    <div className="radio__item">
      <h3>{title}</h3>
      <Radio.Group {...rest}>
        {options.map(item => (
          <Radio key={item.value} value={item.value}>{item.label}</Radio>
        ))}
      </Radio.Group>
    </div>
  )
}
