import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {updateMe, setStatus} from 'mattermost-redux/actions/users';
import {getConfig} from 'mattermost-redux/selectors/entities/general';
import {getCurrentUser, getStatusForUserId} from 'mattermost-redux/selectors/entities/users';

import NewUserSettingsNotifications from './new_user_settings_notifications';

function mapStateToProps(state) {
    const config = getConfig(state);

    const siteName = config.SiteName;
    const sendPushNotifications = config.SendPushNotifications === 'true';
    const enableAutoResponder = config.ExperimentalEnableAutomaticReplies === 'true';

    const currentUser = getCurrentUser(state);

    if (!currentUser) {
        return {};
    }

    const userId = currentUser.id;

    return {
        userId,
        siteName,
        sendPushNotifications,
        enableAutoResponder,
        status: getStatusForUserId(state, userId),
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({
            updateMe,
            setStatus,
        }, dispatch),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(NewUserSettingsNotifications);
