import React, { useReducer, useEffect } from "react";
import moment from "moment";
import { Checkbox, Divider, TimePicker, message } from "antd";
import RadioItems from "../RadioItems";
import "./notific.scss";
const dateDormat = "HH:mm";

const unreadOpts = [
  { label: "", value: "true" },
  { label: "", value: "false" },
];

export default props => {
  // console.log(props, '-------notific props');
  const { desktop, comments, desktop_sound, start_time = '', end_time = '', unread, meeting_notify_status } = props.user.notify_props;
  const [state, setState] = useReducer((o, n) => ({ ...o, ...n }), {
    start_time,
    end_time,
    isEnable: start_time.length > 0 && end_time.length > 0,
    meetingNotifyStatus: meeting_notify_status === 'true',
  })
  const handleChange = (e, data) => {
    const name = e.target.name;
    let val;
    if (e.target.type === "checkbox") {
      val = `${e.target.checked}`
      setState({
        meetingNotifyStatus: e.target.checked,
      })
    } else {
      val = e.target.value
    }
    let d = {
      ...props.user.notify_props,
      ...data,
    }
    if (name) {
      d[name] = val;
    }
    props.actions
      .updateMe({
        notify_props: d,
      })
      .then(({ data: result, error: err }) => {
        if (result) {
          // console.log("notific update ok", result);
        } else if (err) {
          // console.error("notific update fail", err);
          message.error(err);
        }
      });
  };
  const handleCheckbox = e => {
    const val = e.target.checked;
    const name = e.target.name;
    handleChange({
      target: {
        name: e.target.name,
        value: `${val}`
      }
    });
  };
  const handleTimePicker = (e, type) => {
    const val = moment(e).format("HH:mm:ss");
    setState({ [type]: val });
    // console.log(e, 'time')
    if (e != null) {
      if (type === 'start_time' && state.end_time) {
        handleChange({
          target: {
            name: 'start_time',
            value: val,
          }
        }, { end_time: state.end_time })
      }
      if (type === 'end_time' && state.start_time) {
        handleChange({
          target: {
            name: 'end_time',
            value: val,
          }
        }, { start_time: state.start_time })
      }
    }
  };
  const handlePeriod = e => {
    setState({ isEnable: e.target.checked });
    if (!e.target.checked) {
      setState({
        start_time: '',
        end_time: '',
      })
      handleChange({ target: {} }, {
        start_time: '',
        end_time: '',
      })
    }
  };
  return (
    <div className="setting__notific">
      <RadioItems
        title=""
        name="unread"
        options={unreadOpts}
        // value={unreadOpts[0].value}
        defaultValue={unread || 'true'}
        onChange={handleChange}
      />
      {/* <Checkbox name="meeting_notify_status" checked={meeting_notify_status === 'true'} onChange={handleChange}>
          
        </Checkbox> */}
      <RadioItems
        title=""
        name="desktop"
        options={[
          { label: "", value: "all" },
          { label: "", value: "mention" },
          { label: "", value: "none" }
        ]}
        onChange={handleChange}
        defaultValue={desktop}
      />
      <RadioItems
        title=""
        name="comments"
        options={[
          { label: "", value: "any" },
          { label: "", value: "root" },
          { label: "", value: "never" }
        ]}
        onChange={handleChange}
        defaultValue={comments}
      />
      <RadioItems
        title=""
        name="desktop_sound"
        options={[
          { label: "", value: "true" },
          { label: "", value: "false" }
        ]}
        onChange={handleChange}
        defaultValue={desktop_sound}
      />
      <Divider />
      <div className="ant-checkbox-group">
        <div className="time">
          <Checkbox
            name="period"
            onChange={handlePeriod}
            checked={state.isEnable}
          >
            
          </Checkbox>
          <div className="time-pick">
            <TimePicker
              value={state.start_time ? moment(state.start_time, 'HH:mm') : null}
              onChange={e => handleTimePicker(e, "start_time")}
              format={dateDormat}
              placeholder="00:00"
              disabled={!state.isEnable}
            />
            <TimePicker
              value={state.end_time ? moment(state.end_time, 'HH:mm') : null }
              onChange={e => handleTimePicker(e, "end_time")}
              format={dateDormat}
              placeholder="23:59"
              disabled={!state.isEnable}
            />
          </div>
        </div>
        <Checkbox name="meeting_notify_status" checked={meeting_notify_status === 'true'} onChange={handleChange}>
          
        </Checkbox>
      </div>
    </div>
  );
};

